@import url(//netdna.bootstrapcdn.com/font-awesome/4.0.3/css/font-awesome.css);
@import url(https://fonts.googleapis.com/css?family=Titillium+Web:300);
body {
 margin: 0;

 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
}

code {
 font-family: source-code-pro, Menlo, Monaco, Consolas,
  "Courier New", monospace;
}

* {
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}
#a {
 color: black;
 font-size: 12px;
 left: 60px;
}
#b {
 color: #4c75f2;
 font-size: 10px;
 left: 70px;
}
.dec {
 display: table-cell;
 position: relative;
 vertical-align: middle;
 font-weight: bold;
}

.fa-2x {
 font-size: 2em;
}
.side {
 left: 5px;
}
.fa {
 position: relative;
 display: table-cell;
 width: 402px;
 height: 70px;
 text-align: center;
 vertical-align: middle;
 font-size: 25px;
}
.fa:hover {
 color: #4c75f2;
}
.main-menu:hover,
nav.main-menu.expanded {
 width: 250px;
 overflow: visible;
}
.main-menu {
 background: #f6f6f7;
 border-right: 1px solid #e5e5e5;
 position: fixed;
 top: 0;
 bottom: 0;
 height: 100%;
 left: 0;
 width: 87px;
 overflow: hidden;
 -webkit-transition: width 0.05s linear;
 transition: width 0.05s linear;

 z-index: 1000;
}
.main-menu > ul {
 margin: 16px -38px;
}

.main-menu li {
 position: relative;
 display: block;
 width: 250px;
}
.main-menu li > a {
 position: relative;
 display: table;
 border-collapse: collapse;
 border-spacing: 0;
 color: #999;
 font-size: 14px;
 text-decoration: none;
 -webkit-transition: all 0.1s linear;
 transition: all 0.1s linear;
}
.main-menu .nav-text {
 position: relative;
 display: table-cell;
 vertical-align: middle;
 width: 190px;
 font-family: "Titillium Web", sans-serif;
 font-weight: bold;
}
.covid {
 display: flex;
 justify-content: center;
 align-items: center;
 font-weight: bold;
}
.special {
 left: 55px;
 top: 180px;
 font-size: 13px;
}
.top {
 visibility: hidden;
}
@media screen and (max-width: 768px) {
 .top {
  visibility: visible;
 }
 .lef {
  visibility: hidden;
 }
}
nav.main-menu-mobile {
 padding-top: 10px;
 padding-bottom: 10px;
}

li.nav-item {
 font-size: 16px;
 padding-top: 10px;
 padding-left: 10px;
 font-weight: 700;
}
